	module.exports = {"data":{"post":{"html":"<p>Christopher is starting to become frightened of monsters. For example, I had thought that <a href=\"http://us.imdb.com/Title?0198781\">Monsters, Inc.</a> would be a cute video for us to watch together, but after the first two minutes, he demanded that I turn it off. Since then, the couple of toys he has from that movie have been at the bottom the the toy favorite list (as in he throws them down the stairs everytime he notices them). And yesterday's <em>Sesamstrasse</em> had a theme of scary shadows that look like monsters, and the theme did not please him at all. He was impressed by on clip where the monster in Ernie's bedroom turned out to be a quilt. That became his mantra for the next hour. \"Monster. Quilt. Monster. Quilt.\"</p>\n<p>I was reminded of monsters by today's <a href=\"http://www.lileks.com/bleats/archive/02/1002/100202.html#101102\">Bleat</a>, which also provides an accurate description of what happens to a father's psyche when the mother is out of town for several days.</p>","fields":{"title":"Monsters","date":"11 Oct 2002","url":"https://papascott.de/2002-10-11-monsters/"}}},"pathContext":{"slug":"/2002-10-11-monsters/","prev":{"fields":{"title":"Extra Dry","date":"11 Oct 2002","slug":"/2002-10-11-extra-dry/"}},"next":{"fields":{"title":"OpenOffice","date":"11 Oct 2002","slug":"/2002-10-11-openoffice/"}}}}

